<template>
  <mu-container class="default-info-cmd cmd" v-if="parseInt(cmd.visible) === 1">
    <span class="pull-left">
      <span class="line-icon" v-if="cmd.icon != ''" v-html="cmd.icon"></span>
      {{ cmd.name }}
    </span>
    <span class="pull-right">{{ cmd.state }} {{ cmd.unite }}</span>
  </mu-container>
</template>

<script>
/**
 * Show standard command that get numeric data (with unite)
 * @group Commands
 */
export default {
  name: "DefaultInfoCmd",
  props: {
    // Command object
    cmd: null
  },
  mounted() {
    this.$store.commit("addShowedCmd", { cmd: this.cmd });
  }
};
</script>
